<script>
	export default {
		data: function() {
			return {
				imgArr: [],
				mark: 0,
				timer: "",
				cheap: [],
				phone: '',
				jiadian: '',
				zhineng: '',
				dapei: '',
				activeIndex: 4,
				timer: null,
				isTransitioning: false,
				transitionInterval: 500,
				isResetIndex: false,
				len:0
			}

		},
		created() {
			this.play();
			this.toTop();
			this.getCarousel();
			this.getCheap();
			this.getPhone();
			this.getJiadian();
			this.getZhineng();
			this.getDapei();
			
			//this.imgsComputed()
		},
		methods: {
			switchFn(i) {
				//console.log(this.len)
				if(this.isTransitioning) {
					return
				}
				else if(i == 1) {
					this.activeIndex += 4
				}
				else if(i == -1) {
					this.activeIndex -= 4
				}

			},
			getCarousel() {
				this.axios.get('/index/getCarousel')
					.then(res => {
						this.imgArr = res.data
						//console.log(res.data)
					})
			},
			getCheap() {
				this.axios.get('/index/getCheap')
					.then(res => {
						this.cheap = res.data
						for(var i = 0; i < 4; i++) {
							this.cheap.push(this.cheap[i])
							this.cheap.unshift(this.cheap[this.cheap.length - 1 - i])
						}
						this.len = this.cheap.length
					})
			},
			getPhone() {
				this.axios.get('/index/getPhone')
					.then(res => {
						this.phone = res.data
					})
			},
			getJiadian() {
				this.axios.get('/index/getJiadian')
					.then(res => {
						this.jiadian = res.data
					})
			},
			getZhineng() {
				this.axios.get('/index/getZhineng')
					.then(res => {
						this.zhineng = res.data
					})
			},
			getDapei() {
				this.axios.get('/index/getDapei')
					.then(res => {
						this.dapei = res.data
					})
			},
			autoplay() {
				this.mark++;
				if(this.mark == this.imgArr.length)
					this.mark = 0;
			},
			play() {
				this.timer = setInterval(this.autoplay, 3000)
			},
			clearauto() {
				clearInterval(this.timer)
			},
			clickleft() {
				if(this.mark == 0) {
					this.mark = this.imgArr.length - 1;
				}
				else {
					this.mark--;
				}

			},
			clickright() {
				if(this.mark == this.imgArr.length - 1) {
					this.mark = 0;
				}
				else {
					this.mark++;
				}

			},
			setIndex(mark) {
				this.mark = mark;
			},
			mouseenter() {
				this.clearauto();
			},
			mouseleave() {
				this.play();
			},
			toTop() {
				$(window)
					.scroll(function() {
						var scrollValue = $(window)
							.scrollTop();
						scrollValue > 500 ? $('[class=totop]')
							.fadeIn() : $('[class=totop]')
							.fadeOut();
					});
			},
			
		},
		watch: {
			activeIndex(newActiveIndex, oldActiveIndex) {
				if((newActiveIndex === 4 && oldActiveIndex === (this.len - 4)) || (oldActiveIndex === 0 && newActiveIndex === this.len - 8)) {
					this.isResetIndex = true
					return
				}
				this.isResetIndex = false
				this.isTransitioning = true
				setTimeout(() => {
					if(this.activeIndex === 0) {
						this.activeIndex = this.len - 8
					}
					else if(this.activeIndex === (this.len - 4)) {
						this.activeIndex = 4
					}
					this.isTransitioning = false
				}, this.transitionInterval)
			}
		}
	}
</script>

