							<a href="javascript:void(0);" @click.prev="toLogin">登陆</a>
						</li>
						<li>|</li>
						<li>
							<router-link to="/register">注册</router-link>
						</li>
						<li>|</li>
						<li>
							<a href='#' @click.prev="onSelfInfo">个人中心</a>
						</li>

						<li>
