				<div class="search fr">
					<div>
						<div class="form clear">
							<input type="text" name="search" id="search" placeholder="小米8直降200元" autocomplete="off" v-model="search" @keyup.13="toSearch" @input="onchange" @blur="onblur" @focus="onchange"/>
							<input type="button" value="" @click.prev="toSearch" />
						</div>
						<div class="sousuolianxiang" :style="hide">
							<ul>
								<li v-for="(item,index) in searchList"><router-link :to="`/detail/${item.lid}`">{{item.title}}</router-link></li>
								
							</ul>
						</div>
					</div>
				</div>
