	</div>
</template>

<script>
	export default {
		data: function() {
			return {
				path: '',
				islogin: {
					on: false,
					out: true,
				},
				uname: '',
				uid: '',
				count: '',
				search: '',
				searchList:'',
				hide:{display:'none'}
			}
		},
		methods: {
			onchange(){
				this.hide.display = 'block'
				
			},
			onblur(){
				this.hide.display = 'none'
			},
			toLogin() {
				this.path = this.$route.path;
				this.$router.push('/login' + this.path)
			},
			isLogin() {
				this.axios.get('/user/islogin')
					.then(res => {
						//console.log(res.data)
						if(res.data.state == 'ok') {
							this.islogin.on = true;
							this.islogin.out = false;
							this.uname = res.data.uname;
							this.uid = res.data.uid
							this.axios.get('/cart/count')
								.then(res => {
									this.count = res.data.count
								})
						}
						else {
							this.islogin.on = false;
							this.islogin.out = true;
							this.uname = '';
						}
					})
			},
			loginout() {
				this.axios.get('/user/loginout')
					.then(res => {
						this.isLogin();
					})
			},
			onSelfInfo() {
				this.axios.get('/user/islogin')
					.then(res => {
						if(res.data.state == 'ok') {
							this.$router.push('/self_info/' + this.uid)
						}
						else {
							alert('您还没有登陆，请登陆！！！')
							this.toLogin()
						}
					})
			},
			onShoppingCart() {
				this.axios.get('/user/islogin')
					.then(res => {
						if(res.data.state == 'ok') {
							this.$router.push('/shopping_cart')
						}
						else {
							alert('您还没有登陆，请登陆！！！')
							this.toLogin()
						}
					})
			},
			onOrderCenter() {
				this.$router.push('/order_center/' + this.uid)
			},
			toSearch() {
				this.$router.push('/products/' + this.search)
			}
		},
		mounted() {
			this.isLogin();

		},
		watch: {
			search() {
				
				this.axios.get('/products/search', {
						params: {
							search: this.search,
							pno: 0
						}
					})
					.then(res => {
						this.searchList = res.data.products
					})
			},
			'$route' (to, from) {
				this.$router.go(0);
			}
		}
	}
</script>

<style scoped>
	/*------------start header-------*/
	
	header {
