		display: block;
		padding: 0 8px;
	}
	
	header .top .right ul li {
		float: left;
		color: #ccc;
		font-size: 12px;
	}
	
	header .top .right ul li a {
		color: #b0b0b0;
		font-size: 12px;
		display: block;
		padding: 0 8px;
	}
	
