	}
	
	.logoSearch .container .search .sousuolianxiang {
		position: absolute;
		width: 260px;
		height: 150px;
		background: rgba(255, 255, 255, .8);
		z-index: 999;
		overflow: hidden;
	}
	.logoSearch .container .search .sousuolianxiang ul{
		margin-top: 15px;
	}
	.logoSearch .container .search .sousuolianxiang ul li{
		height: 30px;
		line-height: 30px;
		font-size: 18px;
		padding-left:15px ;
		color: #999;
	}
	.logoSearch .container .search .sousuolianxiang ul li:hover{
		background: #FF7F50;
		color: #000;
		cursor: pointer;
	}
	.logoSearch .container .search .form {
		margin-top: 30px;
		border: 1px solid #b0b0b0;
		transition: all .3s;
	}
	
