		border: 1px solid #FF6700;
	}
	
	.logoSearch .container .search input {
		display: block;
		float: left;
		height: 40px;
		border: 0;
		outline: 0;
	}
	
	.logoSearch .container .search input[type="text"] {
		width: 260px;
		padding-left: 10px;
	}
	
	.logoSearch .container .search input[type="button"] {
		width: 45px;
		background: #b0b0b0 url(../../static/img/img-sp_ll.png) no-repeat 1px -5px;
		cursor: pointer;
	}
	/*---------------end logoSearch----------*/
</style>


